.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100vw;
  height: 10vh;
  background-color: rgb(76, 0, 197);
  color: rgb(248, 245, 245);
}
.navbar ul {
  list-style: none;
}
.navbar ul a {
  text-decoration: none;
  color: #c7c7c7;
  transition: 0.15s cubic-bezier(0.6, 0.04, 0.98, 0.335);
}
.navbar ul a:hover, .navbar ul a:focus {
  color: rgb(248, 245, 245);
}